import * as yup from 'yup';

export const FingerPrintSchema = yup.object().shape({
    fingerprintid: yup
        .number()
        .required('Atributo fingerprintid  obrigatrio'),
    uuid: yup.string().required('Atributo uuid  obrigatrio'),
    devicefingerprint: yup
        .string()
        .required('Atributo devicefingerprint  obrigatrio')
});
