import * as yup from 'yup';

export const CreditCardFormCardNumberSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup.string().required('Nmero do carto invlido'),
        gazinEncryptedValue: yup.string().required('Nmero do carto invlido'),
        isValid: yup.boolean().isTrue('Nmero do carto invlido')
    })
    .required('Nmero do carto invlido');

export const CreditCardFormExpiryDateSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup
            .object()
            .shape({
                expiryMonth: yup
                    .string()
                    .required('Data de expirao invlida'),
                expiryYear: yup.string().required('Data de expirao invlida')
            })
            .required('Data de expirao invlida'),
        gazinEncryptedValue: yup.string().required('Data de expirao invlida'),
        isValid: yup.boolean().isTrue('Data de expirao invlida')
    })
    .required('Data de expirao invlida');

export const CreditCardFormSecurityCodeSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup
            .string()
            .required('Cdigo de segurana invlido'),
        gazinEncryptedValue: yup
            .string()
            .required('Cdigo de segurana invlido'),
            isValid: yup.boolean().isTrue('Cdigo de segurana invlido')
    })
    .required('Data de expirao invlida');

export const CreditCardFormNameOnCardSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup.string().required('Nome no carto invlido'),
        gazinEncryptedValue: yup.string().required('Nome no carto invlido'),
        isValid: yup.boolean().isTrue('Nome do carto invlido')
    })
    .required('Nome no carto invlido');
