import * as yup from 'yup';

export const CreditCardFormPagarMeCardNumberSchema = yup
    .object()
    .shape({
        pagarMeValue: yup.string().required('Nmero do carto invlido'),
        isValid: yup.boolean().isTrue('Nmero do carto invlido')
    })
    .required('Nmero do carto invlido');

export const CreditCardFormPagarMeExpiryDateSchema = yup
    .object()
    .shape({
        pagarMeValue: yup
            .object()
            .shape({
                expiryMonth: yup
                    .string()
                    .required('Data de expirao invlida'),
                expiryYear: yup.string().required('Data de expirao invlida')
            })
            .required('Data de expirao invlida'),
        isValid: yup.boolean().isTrue('Data de expirao invlida')
    })
    .required('Data de expirao invlida');

export const CreditCardFormPagarMeSecurityCodeSchema = yup
    .object()
    .shape({
        pagarMeValue: yup.string().required('Cdigo de segurana invlido'),
        isValid: yup.boolean().isTrue('Cdigo de segurana invlido')
    })
    .required('Data de expirao invlida');

export const CreditCardFormPagarMeNameOnCardSchema = yup
    .object()
    .shape({
        pagarMeValue: yup.string().required('Nome no carto invlido'),
        isValid: yup.boolean().isTrue('Nome do carto invlido')
    })
    .required('Nome no carto invlido');
