import * as yup from 'yup';

export const BrowserRequestDataSchema = yup.object().shape({
    remoteaddr: yup.string().required('Atributo remoteaddr  obrigatrio'),
    host: yup.string().required('Atributo host  obrigatrio'),
    serveraddr: yup.string().required('Atributo serveraddr  obrigatrio'),
    servername: yup.string().required('Atributo servername  obrigatrio'),
    port: yup.string().required('Atributo port  obrigatrio'),
    scheme: yup.string().required('Atributo scheme  obrigatrio'),
    useragent: yup.string().required('Atributo useragent  obrigatrio'),
    browser: yup.string().required('Atributo browser  obrigatrio')
});
