import * as yup from 'yup';

export const CreditCardSchema = yup.object().shape({
    holder: yup.string().required('Atributo holder  obrigatrio'),
    brand: yup.string().required('Atributo brand  obrigatrio'),
    insertiontypecardnumber: yup
        .string()
        .required('Atributo insertiontypecardnumber  obrigatrio'),
    encryptedcardnumber: yup
        .string()
        .required('Atributo encryptedcardnumber  obrigatrio'),
    maskedcardnumbervalue: yup
        .string()
        .required('Atributo maskedCardNumberValue  obrigatrio'),
    encryptedexpirymonth: yup
        .string()
        .required('Atributo encryptedexpirymonth  obrigatrio'),
    encryptedexpiryyear: yup
        .string()
        .required('Atributo encryptedexpiryyear  obrigatrio'),
    encryptedsecuritycode: yup
        .string()
        .required('Atributo encryptedsecuritycode  obrigatrio'),
    encryptedcardnumberpag: yup
        .string()
        .required('Atributo encryptedcardnumberpag  obrigatrio'),
    encryptedexpirydatepag: yup
        .string()
        .required('Atributo encryptedexpirydatepag  obrigatrio'),
    encryptedsecuritycodepag: yup
        .string()
        .required('Atributo encryptedsecuritycodepag  obrigatrio')
});
