import * as yup from 'yup';

export const BrowserDataSchema = yup.object().shape({
    hostname: yup.string().required('Atributo hostname  obrigatrio'),
    useragent: yup.string().required('Atributo useragent  obrigatrio'),
    ipaddress: yup.string().required('Atributo ipaddress  obrigatrio'),
    type: yup.string().required('Atributo type  obrigatrio'),
    language: yup.string().required('Atributo language  obrigatrio'),
    cookiesaccepted: yup
        .boolean()
        .required('Atributo cookiesaccepted  obrigatrio'),
    javaenabled: yup.boolean().required('Atributo javaenabled  obrigatrio'),
    screenheight: yup.number().required('Atributo screenheight  obrigatrio'),
    screenwidth: yup.number().required('Atributo screenwidth  obrigatrio'),
    colordepth: yup.number().required('Atributo colordepth  obrigatrio'),
    timezoneoffset: yup
        .number()
        .required('Atributo timezoneoffset  obrigatrio')
});
